import React from 'react'
import './Section.css'

const Section = () => {
  return (
    <div className="full-screen" id='Navbar'>
      <div className="container mt-5">
         <div className="row">
            <div className="col-lg-12 box-1 shadow shadow-dark">
                <div className="h-1 text-center text-white mt-5">
                    <h1>SNIPBOARD<span>.IO</span></h1>
                    <p>The easiest way to share screenshot and images</p>
                </div>
            </div>
            <div className="col-lg-12 box-2 shadow shadow-dark">
                <div className="h-2 mt-2">
                  <p>1. Click on the window you want to capture.</p>
                  <p>2. Press <span>Alt+Print Screen.</span></p>
                  <p>3. Click back on this webpage.</p>
                  <p>4. Press <span>Ctrl+V</span> to upload the image.</p>
                </div>
            </div>
            <div className="col-lg-12 box-3 shadow shadow-dark">
                <div className="h-3 text-center text-white mt-2">
                    <p>Other upload options: &nbsp; <a href="">drag and drop</a> &nbsp; image files onto this page, or &nbsp; <a href="">pick a file</a> &nbsp; from your computer.</p>
                </div>
            </div>
         </div>
      </div>
    </div>
  )
}

export default Section
