import React from 'react'
import './Features.css'
import Keyboard from '../../img/keyboard.png';
import WP from '../../img/wp.png';

const Features = () => {
  return (
    <div className="full-screen-2" id='Features'>
        <div className="container">
            <div className="row">
                <div className="col-lg-12 text-white features">
                  <h1>About Snipboard.io</h1>
                  <p>Snipboard.io is the fastest way to share a screenshot without needing to download
                     or install anything. It's designed with your workflow in mind by using only a 
                     minimum number of keypresses to share your images.
                  </p>
                  <h1 className='mt-5'>Capturing your screen</h1>
                  <p className='changes'>The <span>Print Screen</span> key is your universal screen capture button. It's located near the 
                     top right area of your keyboard, and is sometimes abbreviated to something like
                     <span> "Prt Scr"</span>. 
                  </p>

                  <div className="k-image text-center mt-5">
                    <img src={Keyboard} alt="" width="40%" />
                  </div>
                  <p className='changes mt-5'>You can use it anywhere - you don't need the Snipboard.io website open. 
                     Just press it once and it'll take a snapshot of whatever is currently on your screen and
                     store it in your clipboard.Then, just open up Snipboard.io and paste it by 
                     pressing <span>Ctrl+V</span>.
                  </p>
                  <div className="link text-end">
                  <a href="">Instruction for Mac</a>
                  </div>
                  <h1 className='mt-5'>Features</h1>
                  <p className='changes-2'>• Crop and edit your images with the simple, built-in editor.
                    <br />
                    • <a href="">Register an account</a> to manage and delete your old images.
                    <br />
                    • Designed with power-users in mind, Snipboard.io has a fast workflow 
                    that requires only the minimum number of keypresses.
                  </p>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Features
