import React from 'react'
import './Navbar.css'
import { Link } from 'react-scroll';
const Navbar = () => {
  return (
    <div className="full-screen-7">
    <nav className="navbar navbar-expand-lg fixed-top" >
  <div className="container">
    <a className="navbar-brand text-white" href="#">SNIPBOARD<span>.IO</span></a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse justify-content-end" id="navbarNavDropdown">
      <ul className="navbar-nav">
        <Link spy={true} to='Navbar'>
        <li className="nav-item">
          <a className="nav-link"  href="#">Home</a>
        </li>
        </Link>
        <Link spy={true} to='Features'>
        <li className="nav-item"> 
          <a className="nav-link" href="#">Features</a>
        </li>
        </Link>
        <Link spy={true} to='Pricing'>
        <li className="nav-item">
          <a className="nav-link" href="#">Pricing</a>
        </li>
        </Link>
        <Link spy={true} to='FAQ'>
        <li className="nav-item">
          <a className="nav-link" href="#">FAQ</a>
        </li>
        </Link>
        <Link spy={true} to='Contact'>
        <li className="nav-item">
          <a className="nav-link" href="#">Contact</a>
        </li>
        </Link>
      </ul>
    </div>
  </div>
</nav>
</div>

  );
}

export default Navbar
