import React from 'react'
import './Contact.css'

const Contact = () => {
  return (
    <div className="full-screen-5" id='Contact'>
        <div className="container">
            <div className="row">
                <div className="col-lg-12 everything">
                   <div className="contact mt-5">
                    <h1>Contact Information</h1>
                   </div>
                   <div className="row mt-5">
                    <div className="col-xxl-6 col-xl-6 col-lg-6 col-md-6">
                        <div className="map">
                            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3620.404355348577!2d66.99933651431984!3d24.850035651796837!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3eb33e01777bd7b3%3A0x894b5cdbd0d13dc3!2sAl-Fateem%20Academy%20-%20Web%20Development%20Course%20Training%20Institute!5e0!3m2!1sen!2s!4v1643985598459!5m2!1sen!2s" 
                                width="100%" 
                                height="300" 
                                style={{border: "0"}} 
                                allowfullscreen="" 
                                loading="fast">
                            </iframe>
                        </div>
                    </div>
                    <div className="col-xxl-6 col-xl-6 col-lg-6 col-md-6">
                        <div className="f-1">
                            <h1>Form</h1>
                        </div>
                        <div className="form text-white">
                        <form>
                            <div class="row mb-3">
                                <label for="inputEmail3" class="col-sm-2 col-form-label">Email</label>
                                <div class="col-sm-10">
                                <input type="email" class="form-control" id="inputEmail3"/>
                                </div>
                            </div>
                            <div class="row mb-3">
                                <label for="inputPassword3" class="col-sm-2 col-form-label">Password</label>
                                <div class="col-sm-10">
                                <input type="password" class="form-control" id="inputPassword3"/>
                                </div>
                            </div>
                            <fieldset class="row mb-3">
                                <legend class="col-form-label col-sm-2 pt-0">Radios</legend>
                                <div class="col-sm-10">
                                <div class="form-check">
                                    <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios1" value="option1" checked/>
                                    <label class="form-check-label" for="gridRadios1">
                                    First radio
                                    </label>
                                </div>
                                <div class="form-check">
                                    <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios2" value="option2"/>
                                    <label class="form-check-label" for="gridRadios2">
                                    Second radio
                                    </label>
                                </div>
                                <div class="form-check disabled">
                                    <input class="form-check-input" type="radio" name="gridRadios" id="gridRadios3" value="option3" disabled/>
                                    <label class="form-check-label" for="gridRadios3">
                                    Third disabled radio
                                    </label>
                                </div>
                                </div>
                            </fieldset>
                            <div class="row mb-3">
                                <div class="col-sm-10 offset-sm-2">
                                </div>
                            </div>
                            <button type="submit" class="btn btn-primary">Sign in</button>
                            </form>
                        </div>
                    </div>
                   </div>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Contact
